import React, { FC, useEffect, useState } from "react";
import GoogleMapReact from 'google-map-react';
import { GOOGLE_API_KEY } from "../../config";

import './index.scss'

const Adventures = () => {
  const Map: FC<{ center: { lat: number, lng: number } }> = (props) => {
    return (
      <div className="map">
        <GoogleMapReact
          bootstrapURLKeys={{ key: GOOGLE_API_KEY }}
          center={{ lat: props.center.lat, lng: props.center.lng }}
          defaultCenter={{ lat: 37.7749, lng: -122.4194 }}
          defaultZoom={12}
          draggable={true}
          options={{
            styles: require(`./map.json`),
          }}>
          <Marker lat={37.7749} lng={-122.4194} />
        </GoogleMapReact>
      </div>
    )
  }

  const Marker: FC<{ lat: number, lng: number }> = (props) => {
    return (
      <div style={{ height: "10px", width: "10px", backgroundColor: "red", background: "red" }}>
      </div>
    )
  }

  const [coords, setCoords] = useState({lat: 37.7749, lng: -122.4194})

  useEffect(() => {
    
    setTimeout(() => {
      setCoords({lat: 37.8185, lng: -122.4738})
      console.log("change")
    }, 5000);
  }, []);

  return (
    <div>
      <Map center={coords} />
      <div className="opaqueBg"></div>
    </div>
  );
};

export default Adventures;